import checkedInImageFile from '@/assets/images/ico-support-local-pin.png';
import {
  SPRITE_MARKER_URL,
  MARKER_WIDTH,
  MARKER_HEIGHT,
  OFFSET_X,
  OFFSET_Y,
  OVER_MARKER_WIDTH,
  OVER_MARKER_HEIGHT,
  OVER_OFFSET_X,
  OVER_OFFSET_Y,
  SPRITE_WIDTH,
  SPRITE_HEIGHT,
  SPRITE_GAP,
} from '@/constants/kakaoMap';

// MakrerImage    
export const createMarkerImage = (
  markerSize,
  offset,
  spriteOrigin,
  spriteImageSize,
  imageUrl,
) => {
  if (!window?.kakao || window?.kakao?.maps === null) {
    return;
  }
  const KakaoMapObj = window.kakao.maps;

  const markerImage = new KakaoMapObj.MarkerImage(
    imageUrl || SPRITE_MARKER_URL, //    URL
    markerSize, //  
    {
      offset, //    
      spriteOrigin, //       
      spriteSize: spriteImageSize, //   
    },
  );

  return markerImage;
};

export const getMarkerImages = () => {
  if (!window?.kakao || window?.kakao?.maps === null) {
    return;
  }
  const KakaoMapObj = window.kakao.maps;

  const markerSize = new KakaoMapObj.Size(MARKER_WIDTH, MARKER_HEIGHT); // ,   
  const markerOffset = new KakaoMapObj.Point(OFFSET_X, OFFSET_Y); // ,   
  const overMarkerSize = new KakaoMapObj.Size(
    OVER_MARKER_WIDTH,
    OVER_MARKER_HEIGHT,
  ); //   
  const overMarkerOffset = new KakaoMapObj.Point(OVER_OFFSET_X, OVER_OFFSET_Y); //    
  const spriteImageSize = new KakaoMapObj.Size(SPRITE_WIDTH, SPRITE_HEIGHT); //   

  const gapX = MARKER_WIDTH + SPRITE_GAP; //      X  
  const originY = 0; //   ,    Y 
  const overOriginY = 0; //      Y 
  const normalOrigin = new KakaoMapObj.Point(0, originY); //        
  const clickOrigin = new KakaoMapObj.Point(gapX, originY); //        
  const overOrigin = new KakaoMapObj.Point(gapX * 2, overOriginY); //        

  //    
  //  ,  ,   
  const normalImage = createMarkerImage(
    markerSize,
    markerOffset,
    normalOrigin,
    spriteImageSize,
  );
  const overImage = createMarkerImage(
    overMarkerSize,
    overMarkerOffset,
    overOrigin,
    spriteImageSize,
  );
  const clickImage = createMarkerImage(
    markerSize,
    markerOffset,
    clickOrigin,
    spriteImageSize,
  );
  const checkedInImage = createMarkerImage(
    new KakaoMapObj.Size(40, 46),
    new KakaoMapObj.Point(12, OFFSET_Y),
    normalOrigin,
    new KakaoMapObj.Size(40, 46),
    checkedInImageFile,
  );

  return {
    normalImage,
    overImage,
    clickImage,
    checkedInImage,
  };
};
